function countErrors() {
  var $totalEmpty = $('.stop').length;
  if ($totalEmpty === 0 && $('#f_auto').val() !== '') {
    $('.orderForm__button').addClass('lets-go');
  } else {
    $('.orderForm__button.lets-go').removeClass('lets-go');
  }
}

$(document).ready(function() {
  countErrors();
  var $fAuto = $('#f_auto'),
    $fDealer = $('#f_dealer'),
    $fName = $('#f_name'),
    $fPhone = $('#f_phone'),
    $iDealer = $('#dealer'),
    $iName = $('#name'),
    $iPhone = $('#phone');

  // Выбор машины
  $('.carsCatalog__item').on('click', function() {
    var $this = $(this);
    $('.carsCatalog__item.is-selected').removeClass('is-selected');
    $this.addClass('is-selected');
    $fAuto.val($this.data('auto'));
    $('.error-auto').hide();
    countErrors();
  });
  //////////////////////////

  // Мобильное меню
  $('.header__menu-button').on('click', function() {
    var $this = $(this);
    var $menu = $('.header__menu');
    if ($this.hasClass('is-clicked')) {
      $this.removeClass('is-clicked');
      $menu.css('top', '-100%');
      $('li.is-opened').removeClass('is-opened');
    } else {
      $this.addClass('is-clicked');
      $menu.css('top', '0');
    }
  });
  // Sub меню
  $('.header__menu li').click(function() {
    var $this = $(this);
    if (!$this.hasClass('is-opened')) {
      $('li.is-opened').removeClass('is-opened');
      $this.addClass('is-opened');
    } else {
      $this.removeClass('is-opened');
    }
  });
  //////////////////////////

  // Выбор дилера
  $('.dd-menu li').on('mousedown', function() {
    $iDealer.val($(this).text());
    $fDealer.val($(this).text());
    $('.dealer')
      .removeClass('empty')
      .removeClass('stop');
    countErrors();
  });
  $iDealer.click(function() {
    $(this).attr('readonly', 'readonly');
  });
  $iDealer.blur(function() {
    $(this).removeAttr('readonly', 'readonly');
  });
  //////////////////////////

  // Pop-up

  $('.js-modal-show').click(function() {
    if ($(this).hasClass('lets-go')) {
      $fName.val($iName.val());
      $fPhone.val($iPhone.val());
      $('.js-modal')
        .toggleClass('is-shown')
        .toggleClass('is-hidden');
    }
  });

  $('.js-modal-hide').click(function() {
    $('.js-modal')
      .toggleClass('is-shown')
      .toggleClass('is-hidden');
  });
  //////////////////////////

  // Проварка формы на заполнение
  // $('.orderForm input').blur(function() {
  //   var minChars = $(this).data('min');
  //   if ($(this).val() && $(this).val().length >= minChars) {
  //     $(this)
  //       .parent('fieldset')
  //       .removeClass('empty')
  //       .removeClass('stop');
  //   } else {
  //     $(this)
  //       .parent('fieldset')
  //       .addClass('empty')
  //       .addClass('stop');
  //   }
  //   countErrors();
  // });

  // Проверяем имя
  $('.orderForm input').on('keyup paste blur', function() {
    var minChars = $(this).data('min');
    if ($(this).val() && $(this).val().length >= minChars) {
      $(this)
        .parent('fieldset')
        .removeClass('empty')
        .removeClass('stop');
    } else {
      $(this)
        .parent('fieldset')
        .addClass('empty')
        .addClass('stop');
    }
    countErrors();
  });

  //////////////////////////

  $(document).on('submit', '#confirmForm', function(e) {
    $('#submit')
      .prop('disabled', true)
      .val('Надсилаємо...');
    e.preventDefault();
    var data = new FormData(this);
    $.ajax({
      url: 'send.php',
      type: 'POST',
      data: data,
      contentType: false,
      cache: false,
      processData: false,
      success: function(data) {
        console.log(data);
        if (data === 'ok') {
          $('.confirmation').hide();
          $('.thankyou').show();
        }
      },
    });
  });
});
